
'use client';

import { useState, useEffect, useCallback } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogDescription } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { AlertCircle, PlusCircle, User, Stethoscope, Pill, Hash, Loader2, Trash2, Search, CheckCircle2, Upload, FilePlus } from 'lucide-react';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Badge } from '@/components/ui/badge';
import { checkInteractions } from '@/app/actions';
import { type DetectDrugInteractionsOutput } from '@/ai/flows/detect-drug-interactions';
import { PrescriptionUpload } from './prescription-upload';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

interface Medication {
  id: number;
  name: string;
  rxNumber: string;
  doctor: string;
  pills: number;
  dosage: string;
  frequency: string;
  form: string;
}

const initialMedications: Medication[] = [
  { id: 1, name: 'Lisinopril', rxNumber: 'RX789123', doctor: 'Dr. Smith', pills: 30, dosage: '10mg', frequency: 'Once daily', form: 'Tablet' },
  { id: 2, name: 'Metformin', rxNumber: 'RX456789', doctor: 'Dr. Smith', pills: 60, dosage: '500mg', frequency: 'Twice daily', form: 'Tablet' },
  { id: 3, name: 'Simvastatin', rxNumber: 'RX123456', doctor: 'Dr. Jones', pills: 30, dosage: '20mg', frequency: 'Once daily', form: 'Tablet' },
];

export function PatientDashboard() {
  const [patientName, setPatientName] = useState('Jane Doe');
  const [membershipNumber, setMembershipNumber] = useState('MEM123456789');
  const [medications, setMedications] = useState<Medication[]>(initialMedications);
  const [newDrugDetails, setNewDrugDetails] = useState({
    name: '',
    dosage: '',
    frequency: '',
    form: '',
    rxNumber: '',
    doctor: '',
    pills: '',
  });

  const [isLoading, setIsLoading] = useState(false);
  const [interactions, setInteractions] = useState<DetectDrugInteractionsOutput['interactions']>([]);
  const { toast } = useToast();

  const [drugA, setDrugA] = useState('');
  const [drugB, setDrugB] = useState('');
  const [quickCheckResult, setQuickCheckResult] = useState<DetectDrugInteractionsOutput['interactions'][0] | null>(null);
  const [isQuickChecking, setIsQuickChecking] = useState(false);
  const [noInteractionFound, setNoInteractionFound] = useState(false);
  
  const [isAddDrugOpen, setIsAddDrugOpen] = useState(false);
  const [isUploadOpen, setIsUploadOpen] = useState(false);

  const runInteractionCheck = useCallback(async (drugList: string[]) => {
    if (drugList.length < 2) {
      setInteractions([]);
      return;
    }
    setIsLoading(true);
    const response = await checkInteractions(drugList);
    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
      setInteractions([]);
    } else {
      setInteractions(response.interactions || []);
      if (response.interactions && response.interactions.length > 0) {
        toast({ title: 'Interaction Alert', description: 'A potential drug interaction was detected. Please review your medications.' });
      }
    }
    setIsLoading(false);
  }, [toast]);

  useEffect(() => {
    localStorage.setItem('medications', JSON.stringify(medications));
  }, [medications]);

  useEffect(() => {
    if (medications.length > 1) {
      runInteractionCheck(medications.map(m => m.name));
    }
  }, [medications, runInteractionCheck]);

  const handleDetailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setNewDrugDetails(prev => ({ ...prev, [name]: value }));
  };

  const handleAddDrug = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newDrugDetails.name) {
      toast({ variant: 'destructive', title: 'Error', description: 'Please enter a drug name.' });
      return;
    }
    const newMed: Medication = {
      id: Date.now(),
      name: newDrugDetails.name,
      dosage: newDrugDetails.dosage || 'N/A',
      frequency: newDrugDetails.frequency || 'N/A',
      form: newDrugDetails.form || 'Unknown',
      rxNumber: newDrugDetails.rxNumber || 'N/A (Self-Added)',
      doctor: newDrugDetails.doctor || 'Self-Added',
      pills: newDrugDetails.pills ? parseInt(newDrugDetails.pills, 10) : 0,
    };
    
    const updatedMeds = [...medications, newMed];
    setMedications(updatedMeds);
    setNewDrugDetails({ name: '', dosage: '', frequency: '', form: '', rxNumber: '', doctor: '', pills: '' }); // Reset form
    setIsAddDrugOpen(false); // Close dialog
    
    await runInteractionCheck(updatedMeds.map(m => m.name));
  };


  const removeDrug = (id: number) => {
    const updatedMeds = medications.filter(med => med.id !== id);
    setMedications(updatedMeds);
    runInteractionCheck(updatedMeds.map(m => m.name));
  };

  const handleDrugsParsed = (parsedDrugs: Partial<Medication>[]) => {
    if (parsedDrugs.length === 0) {
      toast({ variant: 'destructive', title: 'No Drugs Found', description: "We couldn't find any medication names in the image." });
      return;
    }

    const newMeds: Medication[] = parsedDrugs.map(drug => ({
      id: Date.now() + Math.random(),
      name: drug.name || 'Unknown Drug',
      rxNumber: drug.rxNumber || 'N/A (From Upload)',
      doctor: drug.doctor || 'From Prescription',
      pills: drug.pills || 0,
      dosage: drug.dosage || 'N/A',
      frequency: drug.frequency || 'N/A',
      form: drug.form || 'Unknown',
    }));

    const updatedMeds = [...medications, ...newMeds];
    setMedications(updatedMeds);
    
    setIsUploadOpen(false); // Close dialog
    runInteractionCheck(updatedMeds.map(m => m.name));
    toast({ title: 'Medications Added', description: `${newMeds.length} medication(s) have been added to your list.` });
  };


  const getInteractionStatus = (drugName: string) => {
    return interactions.some(int => int.drugA.toLowerCase() === drugName.toLowerCase() || int.drugB.toLowerCase() === drugName.toLowerCase());
  };

  const handleQuickCheck = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!drugA || !drugB) {
      toast({
        variant: 'destructive',
        title: 'Input Error',
        description: 'Please enter both drug names to check.',
      });
      return;
    }
    setIsQuickChecking(true);
    setQuickCheckResult(null);
    setNoInteractionFound(false);

    const response = await checkInteractions([drugA, drugB]);

    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
    } else if (response.interactions && response.interactions.length > 0) {
      setQuickCheckResult(response.interactions[0]);
    } else {
      setNoInteractionFound(true);
      toast({
        title: 'No Interactions Found',
        description: `No significant interactions were found between ${drugA} and ${drugB}.`,
      });
    }
    setIsQuickChecking(false);
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
       <div className="lg:col-span-1 space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2"><User /> Patient Information</CardTitle>
            </CardHeader>
            <CardContent className="grid md:grid-cols-2 gap-4">
              <div className="space-y-2 md:col-span-2">
                  <Label htmlFor="patientName">Patient Name</Label>
                  <Input 
                      id="patientName" 
                      value={patientName} 
                      onChange={(e) => setPatientName(e.target.value)}
                      placeholder="Enter patient name"
                  />
              </div>
              <div className="space-y-2 md:col-span-2">
                  <Label htmlFor="membershipNumber">Membership No.</Label>
                  <Input 
                      id="membershipNumber" 
                      value={membershipNumber}
                      onChange={(e) => setMembershipNumber(e.target.value)}
                      placeholder="Enter membership number"
                  />
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle>Medication Tools</CardTitle>
              <CardDescription>Use these tools to manage your medication list.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-2">
                <Dialog open={isAddDrugOpen} onOpenChange={setIsAddDrugOpen}>
                  <DialogTrigger asChild>
                    <Button variant="outline" className="w-full justify-start text-left p-4 h-auto">
                        <div className="flex gap-4 items-center">
                          <FilePlus className="h-8 w-8 text-primary" />
                          <div>
                            <p className="font-semibold">Add Medication</p>
                            <p className="text-xs text-muted-foreground">Manually add a drug to your list.</p>
                          </div>
                        </div>
                    </Button>
                  </DialogTrigger>
                  <DialogContent className="sm:max-w-[625px]">
                    <DialogHeader>
                      <DialogTitle>Add New Medication</DialogTitle>
                      <DialogDescription>
                        Enter the details of your medication below.
                      </DialogDescription>
                    </DialogHeader>
                     <form onSubmit={handleAddDrug}>
                        <div className="space-y-4 py-4">
                            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                <div className="space-y-2">
                                    <Label htmlFor="name-modal">Drug Name</Label>
                                    <Input id="name-modal" name="name" placeholder="e.g., Aspirin" value={newDrugDetails.name} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                <div className="space-y-2">
                                    <Label htmlFor="dosage-modal">Dosage</Label>
                                    <Input id="dosage-modal" name="dosage" placeholder="e.g., 10mg" value={newDrugDetails.dosage} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                <div className="space-y-2">
                                    <Label htmlFor="frequency-modal">Frequency</Label>
                                    <Input id="frequency-modal" name="frequency" placeholder="e.g., Once daily" value={newDrugDetails.frequency} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                <div className="space-y-2">
                                    <Label htmlFor="form-modal">Form</Label>
                                    <Input id="form-modal" name="form" placeholder="e.g., Tablet" value={newDrugDetails.form} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                 <div className="space-y-2">
                                    <Label htmlFor="rxNumber-modal">RX Number</Label>
                                    <Input id="rxNumber-modal" name="rxNumber" placeholder="e.g., RX123456" value={newDrugDetails.rxNumber} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                <div className="space-y-2">
                                    <Label htmlFor="doctor-modal">Prescribing Doctor</Label>
                                    <Input id="doctor-modal" name="doctor" placeholder="e.g., Dr. Smith" value={newDrugDetails.doctor} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                                <div className="space-y-2 sm:col-span-2">
                                    <Label htmlFor="pills-modal">Pill Count</Label>
                                    <Input id="pills-modal" name="pills" type="number" placeholder="e.g., 30" value={newDrugDetails.pills} onChange={handleDetailChange} disabled={isLoading} />
                                </div>
                            </div>
                            <Button type="submit" disabled={isLoading} className="w-full">
                                {isLoading ? <Loader2 className="animate-spin mr-2" /> : <PlusCircle className="mr-2" />}
                                Add to List and Check
                            </Button>
                        </div>
                    </form>
                  </DialogContent>
                </Dialog>

                <Dialog open={isUploadOpen} onOpenChange={setIsUploadOpen}>
                  <DialogTrigger asChild>
                      <Button variant="outline" className="w-full justify-start text-left p-4 h-auto">
                        <div className="flex gap-4 items-center">
                          <Upload className="h-8 w-8 text-primary" />
                          <div>
                            <p className="font-semibold">Upload Prescription</p>
                            <p className="text-xs text-muted-foreground">Scan an image of your prescription.</p>
                          </div>
                        </div>
                      </Button>
                  </DialogTrigger>
                  <DialogContent>
                      <DialogHeader>
                        <DialogTitle>Upload Doctor's Prescription</DialogTitle>
                        <DialogDescription>
                          Our AI will scan the image and add the medications to your list automatically.
                        </DialogDescription>
                      </DialogHeader>
                      <PrescriptionUpload onDrugsParsed={handleDrugsParsed} />
                  </DialogContent>
                </Dialog>

                <Dialog>
                  <DialogTrigger asChild>
                     <Button variant="outline" className="w-full justify-start text-left p-4 h-auto">
                        <div className="flex gap-4 items-center">
                          <Search className="h-8 w-8 text-primary" />
                          <div>
                            <p className="font-semibold">Quick Interaction Check</p>
                            <p className="text-xs text-muted-foreground">Check two drugs without saving.</p>
                          </div>
                        </div>
                      </Button>
                  </DialogTrigger>
                  <DialogContent>
                      <DialogHeader>
                        <DialogTitle>Quick Interaction Check</DialogTitle>
                        <DialogDescription>
                          Quickly check for an interaction between two drugs without adding them to your list.
                        </DialogDescription>
                      </DialogHeader>
                       <div className="pt-4 space-y-4">
                        <form onSubmit={handleQuickCheck}>
                            <div className="space-y-4">
                                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                    <div className="space-y-2">
                                        <Label htmlFor="drugA">Drug A</Label>
                                        <Input
                                            id="drugA"
                                            placeholder="e.g., Lisinopril"
                                            value={drugA}
                                            onChange={(e) => setDrugA(e.target.value)}
                                            disabled={isQuickChecking}
                                        />
                                    </div>
                                    <div className="space-y-2">
                                        <Label htmlFor="drugB">Drug B</Label>
                                        <Input
                                            id="drugB"
                                            placeholder="e.g., Ibuprofen"
                                            value={drugB}
                                            onChange={(e) => setDrugB(e.target.value)}
                                            disabled={isQuickChecking}
                                        />
                                    </div>
                                </div>
                                <Button type="submit" disabled={isQuickChecking} className="w-full">
                                    {isQuickChecking ? <Loader2 className="animate-spin mr-2" /> : <Search className="mr-2" />}
                                    Check for Interaction
                                </Button>
                            </div>
                        </form>

                        {isQuickChecking && (
                            <div className="flex justify-center items-center pt-4">
                                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                                <span>Checking...</span>
                            </div>
                        )}

                        {quickCheckResult && (
                            <Card className="mt-4 bg-muted/50">
                                <CardHeader>
                                    <CardTitle className="flex items-center justify-between text-lg">
                                        Interaction Result
                                        <Badge variant={
                                            quickCheckResult.riskLevel === 'High' ? 'destructive' :
                                            quickCheckResult.riskLevel === 'Moderate' ? 'warning' : 'secondary'
                                        }>
                                            {quickCheckResult.riskLevel} Risk
                                        </Badge>
                                    </CardTitle>
                                </CardHeader>
                                <CardContent className="space-y-2">
                                    <p>An interaction was found between <strong>{quickCheckResult.drugA}</strong> and <strong>{quickCheckResult.drugB}</strong>.</p>
                                    <p className="text-muted-foreground text-sm">{quickCheckResult.description}</p>
                                    {quickCheckResult.alternatives && quickCheckResult.alternatives.length > 0 && (
                                        <div className="pt-2">
                                            <h4 className="font-semibold">Suggested Alternatives:</h4>
                                            <p className="text-muted-foreground text-sm">{quickCheckResult.alternatives.join(', ')}</p>
                                        </div>
                                    )}
                                </CardContent>
                            </Card>
                        )}

                        {noInteractionFound && (
                            <Alert className="mt-4">
                                <CheckCircle2 className="h-4 w-4" />
                                <AlertTitle>No Significant Interactions Found</AlertTitle>
                                <AlertDescription>
                                    No significant interactions were found between {drugA} and {drugB}. Always consult your healthcare provider before making any changes to your medication regimen.
                                </AlertDescription>
                            </Alert>
                        )}
                      </div>
                  </DialogContent>
                </Dialog>
            </CardContent>
          </Card>
       </div>
      
        <div className="lg:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle>My Medications</CardTitle>
              <CardDescription>
                A list of your current medications. Interaction alerts will appear here.
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
               <div className="grid md:grid-cols-1 lg:grid-cols-2 gap-4">
                {medications.map((med) => (
                  <Card key={med.id} className="p-4 flex flex-col justify-between">
                    <div>
                      <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-bold text-lg flex items-center gap-2">
                              <Pill size={20} /> {med.name}
                            </h3>
                             <p className="text-sm text-muted-foreground">
                              {med.dosage} - {med.frequency} - {med.form}
                            </p>
                          </div>
                           <Button variant="ghost" size="icon" onClick={() => removeDrug(med.id)} className="text-muted-foreground hover:text-destructive flex-shrink-0">
                              <Trash2 size={16} />
                          </Button>
                      </div>
                       {getInteractionStatus(med.name) && (
                          <Badge variant="destructive" className="flex items-center gap-1 mt-2 w-fit">
                              <AlertCircle size={14} /> Interaction Alert
                          </Badge>
                      )}
                    </div>
                    <Accordion type="single" collapsible className="w-full mt-2">
                        <AccordionItem value="item-1" className="border-b-0">
                            <AccordionTrigger className="text-sm pt-2 pb-0">View Details</AccordionTrigger>
                            <AccordionContent className="text-muted-foreground space-y-1 pt-2 text-sm">
                              <p className="flex items-center gap-2"><Hash size={14}/> <strong>RX Number:</strong> {med.rxNumber}</p>
                              <p className="flex items-center gap-2"><Stethoscope size={14} /> <strong>Prescribed by:</strong> {med.doctor}</p>
                              <p className="flex items-center gap-2"><Pill size={14} /> <strong>Pill Count:</strong> {med.pills}</p>
                            </AccordionContent>
                        </AccordionItem>
                    </Accordion>
                  </Card>
                ))}
              </div>
              {medications.length === 0 && (
                  <div className="text-center py-8 text-muted-foreground border-2 border-dashed rounded-lg">
                      You have no medications saved.
                  </div>
              )}
            </CardContent>
          </Card>
        </div>
    </div>
  );
}

    

    